.portfolio-card {
  padding: 0 10px;
  position: relative;

  &:after {
    clear: both;
    content: "";
    display: table;
  }

  &__all-link {
    width: 100%;
    padding-top: 10px;
    text-align: center;

    & a {
      display: inline-block;
      padding: 20px 40px;
      background: #3BC391;
      color: #fff;
      text-decoration: none;
      transition: all 0.3s cubic-bezier(.25,.8,.25,1);

      &:hover {
        background: #3BC391;
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }

      &:active {
        background: #33a97e;
      }

    }

  }

  & h2 {
    position: absolute;
    top: -60px;
    color: #fff;
    margin: 0;
    padding-left: 40px;
    font-weight: 300;
    font-size: 30px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
  }

  &__item {
    float: left;
    width: 33.33%;
    overflow: hidden;
    position: relative;
    cursor: pointer;

    &:hover &-border__right-top {
      width: 100%;
      transition: 0.2s ease 0s;
    }

    &:hover &-border__right {
      height: 100%;
      transition: 0.2s ease 0.2s
    }

    &:hover &-border__right-bottom {
      width: 100%;
      transition: 0.2s ease 0.4s;
    }

    &:hover &-border__left {
      height: 100%;
      transition: 0.2s ease 0.6s
    }

    &:hover img {
      transform: scale(1.1);
    }

    &:hover &-bg {
      background: rgba(0,0,0,0);
    }

    &-info {
      position: absolute;
      bottom: 0;
      padding: 40px;
      z-index: 2;

      & h3 {
        margin: 0;
        color: #fff;
        font-size: 22px;
        font-weight: 500;
        line-height: normal;
        text-shadow: 1px 1px 1px rgba(0,0,0,.5);
      }

    }

    &-bg {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0px;
      left: 0px;
      background: rgba(0,0,0,0.3);
      transition: all 2s ease 0s;
    }

    &-border {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0px;
      left: 0px;

      &__right-top {
        width: 0px;
        height: 10px;
        position: absolute;
        top: 0px;
        left: 0%;
        background: #3BC391;
        transition: 0.2s ease 0.6s;
      }

      &__right{
        width: 10px;
        height: 0%;
        position: absolute;
        top: 0px;
        right: 0px;
        background: #3BC391;
        transition: 0.2s ease 0.4s
      }
      &__right-bottom{
        width: 0px;
        height: 10px;
        position: absolute;
        bottom: 0px;
        right: 0px;
        background: #3BC391;
        transition: 0.2s ease 0.2s
      }
      &__left{
        width: 10px;
        height: 0%;
        position: absolute;
        bottom: 0px;
        left: 0px;
        background: #3BC391;
        transition: 0.2s ease 0s
      }
    }

    & img {
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 100%;
      transition: transform 3s ease 0s;
      vertical-align:bottom
    }

  }

}


.portfolio {

  &__container {
    padding: 20px;
    margin: 0 auto;
    max-width: 1200px;
    background: #fff;
  }

  &__title {
    font-size: 36px;
    font-weight: 400;
    margin: 0;
    padding: 20px 0;
  }

  &__info-text {
    padding: 0 20px 0 0;

    &_list {
      padding-left: 0;
    }

    & h2 {
      margin: 0;
    }

  }

  &__info {
    position: relative;
    padding: 20px 0;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

    & img {
      width: 100%;
      padding: 0;
    }

  }

  &__cl {
    display: block;
    float: left;
    width: 50%;
  }

  &__cr {
    position: relative;
    display: block;
    float: left;
    width: 50%;
  }

  &__detail-link {
      display: inline-block;
      padding: 20px 40px;
      margin-bottom: 20px;
      background: #3BC391;
      color: #fff !important;
      text-decoration: none;
      transition: all 0.3s cubic-bezier(.25,.8,.25,1);

      &:hover {
        background: #3BC391;
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }

      &:active {
        background: #33a97e;
      }
  }

  &__content-list {
    color: #333;

    & h2 {
      line-height: 36px;
    }

    & hr {
      display: block;
      height: 1px;
      border: 0;
      border-top: 1px solid #ccc;
      margin: 1em 0;
      padding: 0;
    }

    & p {
      padding: 0;
      font-size: 16px;
      line-height: 26px;
    }

    & img {
      width: 100%;
      padding: 0;
    }
  }

  &__content {
    color: #333;

    & h2 {
      margin-top: 60px;
      line-height: 36px;

      &:first-child {
        margin: 0;
      }

    }

    & hr {
      display: block;
      height: 1px;
      border: 0;
      border-top: 1px solid #ccc;
      margin: 1em 0;
      padding: 0;
    }

    & p {
      padding: 10px 0;
      font-size: 16px;
      line-height: 26px;
    }

    & img {
      width: 100%;
      padding: 0;
    }

    & a {
      color: #0277BD;
      text-decoration: none;
    }

    & ol {
      line-height: 26px;
      padding: 0 20px 20px 20px;
    }

    & ul {
      line-height: 26px;
      padding: 0 20px 20px 20px;
    }

  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

  .portfolio {

    &__cl {
      width: 50%;
    }

    &__cr {
      width: 50%;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .portfolio-card {

    & h2 {
      font-size: 22px;
      padding-left: 20px;
    }

    &__item {
      width: 50%;

      &-info {

        & h3 {
          font-size: 18px;
          //font-weight: 300;
        }

      }

    }

  }

  .portfolio {

    &__container {
      padding: 10px;
    }

    &__cl {
      width: 100%;
    }

    &__cr {
      width: 100%;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {

  .portfolio-card {

    &__item {
      width: 100%;
    }

  }

  .portfolio {

    &__cl {
      width: 100%;
    }

    &__cr {
      width: 100%;
    }

  }
}

