.about {

  &__info {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px 0 20px;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

    & h2 {
      margin: 0 20px 20px 0;
      font-size: 40px;
      font-weight: 400;
    }

    & p {
      font-size: 16px;
      line-height: 30px;
    }

  }

  &__cl {
    display: block;
    float: left;
    width: 66.66%;
    padding: 0 40px 0 0;
  }
  &__cr {
    display: block;
    float: left;
    width: 33.33%;
  }

  &__contact-info {
    line-height: 30px;

    & p {
      font-size: 16px;
    }

  }

  &__contact {

    & h3 {
      font-weight: 600;
    }

    & a {
      color: #0277BD;
      text-decoration: none;
    }

  }

  &__form {
      background:#fff;
      padding: 20px;
      margin-bottom: 20px;

    & fieldset {
      border: medium none !important;
      margin: 0 0 10px;
      min-width: 100%;
      padding: 0;
      width: 100%;
    }

    & input, & textarea {
      width:100%;
      border:1px solid #CCC;
      background:#FFF;
      margin:0 0 5px;
      padding:10px;
      outline: 0;
      transition:border-color 0.2s ease-in-out;

      &:hover {
        border: 1px solid #AAA;
      }

      &:focus {
        border: 1px solid #3BC391;
      }

    }

    & button {
      display: block;
      width: 100%;
      padding: 20px 40px;
      background: #3BC391;
      border: none;
      color: #fff;
      text-decoration: none;
      transition: all 0.3s cubic-bezier(.25,.8,.25,1);
      outline: 0;

      &:hover {
        background: #3BC391;
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }

      &:active {
        background: #33a97e;
      }
    }

  }

}
