.header {
  padding: 10px 10px 0 10px;
  //background: #f5f5f5;
  //min-height: 100vh;


  .header__bg {
    position: relative;
    height: 100%;
    top: 0;
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    background-color: #777777;

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      content: "";
      background-color: rgba(0, 0, 0, 0.5);
    }

  }

  &__page {
    padding: 0;
  }

  &__container {
    position: relative;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

  }

  &__c-3-12 {
    display: block;
    float: left;
    width: 33.33%;
  }
  &__c-6-12 {
    position: relative;
    display: block;
    float: left;
    width: 66.66%;
  }

  &__logo {
    display: inline-block;
    margin: 50px 40px 0 40px;
    text-decoration: none;

    & img {
      width: 380px;
      height: auto;
      padding-bottom: 5px;
    }

    & span {
      display: block;
      padding-top: 5px;
      font-size: 20px;
      font-weight: 300;
      line-height: 22px;
      color: #fff;
      border-top: 1px solid rgba(255,255,255,0.5);
      text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
    }

  }

  &__main-nav-btn {
    position: fixed;
    z-index: 1;
    left: 10px;
    display: none;
    color: #fff;
    background: #3BC391;
    font-weight: 400;
    font-size: 18px;
    text-transform: uppercase;
    padding: 15px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
    cursor: pointer;

    &:hover {
      background: #33a97e;
    }

  }

  &__main-nav {

    & ul {
      list-style: none;
      line-height: 36px;
      padding: 20px 40px;
      margin: 0;

      &:after {
        clear: both;
        content: "";
        display: table;
      }

      & li {
        display: block;

        & a {
          font-weight: 400;
          font-size: 18px;
          text-transform: uppercase;
          text-decoration: none;
          color: #fff;
          transition: all .2s;
          text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);

          &:hover {
            color: #3BC391;
          }

        }

      }

    }

    &_page {

      & ul {
        padding-bottom: 20px;

        & li {
          float: left;
          padding-right: 20px;

          & a {
            font-weight: 400;
            font-size: 18px;
            text-transform: uppercase;
            text-decoration: none;
            color: #fff;
            transition: all .2s;
            text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);

            &:hover {
              color: #3BC391;
            }

          }

        }

      }
    }

  }

  &__contact-info {
    display: block;
    padding: 40px 40px 0 0;
    text-align: right;
    font-size: 18px;
    color: #fff;
    line-height: 30px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);

    &_main {
      padding-top: 40px;
    }

    & a {
      text-decoration: none;
      color: #fff;
    }

  }

  &__slogan {
    padding: 200px 50px 200px 50px;
  }

  &__main-h {
    margin: 0;
    padding-bottom: 40px;
    color: #fff;
    font-size: 36px;
    font-weight: 300;
    line-height: normal;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
  }

  &__nav {

    & a {
      display: inline-block;
      margin: 20px 80px 0 0;
      text-align: center;
      text-decoration: none;
      color: #fff;
      transition: all .2s;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        color: #33a97e;
      }

      & img {
        width: 100px;
        height: 100px;
      }

      & h2 {
        font-weight: 400;
        font-size: 14px;
        text-transform: uppercase;
        text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
      }

    }

  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
  .header {
    background: #fff;

    &__slogan {
      padding: 50px 20px 100px 20px;
      text-align: center;
    }

    &__nav {
      //text-align: center;

      & a {

        & img {
          width: 70px;
          height: 70px;
        }

      }

    }

    &__main-h {
      font-size: 26px;
      padding-bottom: 0;
    }

    &__main-nav-btn {
      display: inline-block;
    }

    &__main-nav {
      position: fixed;
      height: 100vh;
      background: #3BC391;
      top: 0;
      z-index: 4;
      overflow: auto;
      transform: translateX(-210px);
      transition: all .1s ease-out;


      &_open {
        transform: translateX(0px);
      }

      & ul {
        padding: 20px 0;
        margin: 0;
        width: 200px;

        & li {
          display: block;
          padding: 0;

          & a {
            display: inline-block;
            padding: 5px 20px;

            &:hover {
              color: #eeeeee;
            }

          }

        }

      }

    }

    &__contact-info {
      padding: 0 40px 40px 0;

      &_main {
        padding-top: 0px;
      }

    }

    &__logo {
      display: block;
      margin: 50px 40px 20px 40px;

      & img {
        width: 300px;
      }

      & span {
        padding-top: 5px;
        font-size: 16px;
      }

    }

    &__c-3-12 {
      width: 100%;
    }
    &__c-6-12 {
      width: 100%;
    }
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {
  .header {

    &__contact-info {
      padding: 5px 20px 20px 20px;
      font-size: 14px;
      line-height: 22px;

      &_main {
        //padding-top: 20px;
      }

    }

    &__logo {
      display: block;
      text-align: center;
      margin: 0 auto;
      padding: 40px 20px 20px 20px;

      & img {
        display: inline-block;
        width: 250px;
      }

      & span {
        display: block;
        border-top: 1px solid rgba(255,255,255,0.5);
        padding-top: 5px;
        font-weight: 400;
        font-size: 14px;
      }

    }

    &__nav {
      text-align: center;

      & a {
        display: block;
        margin: 40px 0;

        & img {
          width: 70px;
          height: 70px;
        }

      }

    }

  }

}

